import * as util from "./util.js"
import { MBP ,Buffer} from 'meta-buffer-pack'
import EventEmitter from "eventemitter3/umd/eventemitter3.js";
import {  SEA  , MsgType, Meta } from "boho";
import {  ENC_MODE } from './states.js';

import * as boho from 'boho'

const encoder = new TextEncoder()
const decoder = new TextDecoder()
const NB = MBP.NB
const MB = MBP.MB
const MBA = MBP.MBA

export class Remocon extends EventEmitter{
  static BOHO = boho
  constructor(url , protocols =[]) {
    super();
    this.ws;
    // this.ssid = 0; // serverside websocket ID
    this.sea = new SEA()
    this.isSecureLine = false // true if connection opened with wss:// protocol
    this.encMode = ENC_MODE.AUTO; // use encryption after auth. if not use secureline

    this.nick = "";
    // this.encMode = MODE.ENC_AUTO
    this.lsid = Remocon.getID(); //local Remocon instance index id.
    this.protocols = protocols 
    this.lastState = false
    this.maxPayload = 1000;
    this.serverURL = url;
    this.txCounter = 0;
    this.rxCounter = 0;
    this.lastTxRxTime = Date.now();
    this.lastTxRxTimeOut = 65000;  // timeout after trx.
    this.runCheckPeriod = 3000 ;//not ping period, only check state. + +Math.trunc(Math.random() * 2000);
    this.runCheckIntervalID = null;
    this.isNode = false;
    this.channels = new Set()
    this.promiseMap = new Map()
    this.promiseTimeOut = 2000
    this.mid = 0
  
    if (typeof WebSocket === "undefined") this.isNode = true;
    Remocon.ls.add(this);
    this.connect();

  }

  static ls = new Set();
  static ID = 1;
  static NodeWebSocket;
  
  static getID() {
    return Remocon.ID++;
  }
  static getByteSize(data){
    return util.getByteSize(data)
  }
  static timeStamp(){
    return util.timeStamp()
  }
  static buf2hex(data){
    return util.buf2hex(data)
  }

  // static u8concat(...data){
  //   return MBP.U8pack(...data) 
  // }

  static PU8(data){
    return MBP.U8(data)
  }

  runChecker(url) {
    // console.log('.')
    if (this.ws?.readyState != 1) {
      this.connect();
    } else {
      let time = Date.now() - this.lastTxRxTime;
      if (time > this.lastTxRxTimeOut ) {
        this.echo();
        // this.echoFrame()
      }
    }

  }

  close() {
    let code = 1000;
    let reason = "well done";
    this.ws?.close(code, reason);
    this.ws = null;
    clearInterval(this.runCheckIntervalID);
    this.runCheckIntervalID = null
  }

  open(url){
    this.connect(url)
  }

  connect(url) {
    if (url) this.serverURL = url;

   if(this.runCheckIntervalID)  clearInterval(this.runCheckIntervalID);
   this.runCheckIntervalID = setInterval(this.runChecker.bind(this), this.runCheckPeriod);

   console.log('+')
    this.ws?.close()
    this.ws = null;
 
    if (this.isNode) {
      this.ws = new Remocon.NodeWebSocket(this.serverURL   , this.protocols);
      this.ws.on("message", this.onWebSocketMessage.bind(this));
    } else {
      this.ws = new WebSocket(this.serverURL , this.protocols );
      this.ws.addEventListener("message", this.onWebSocketMessage.bind(this));
    }
    this.ws.binaryType = "arraybuffer";

    this.ws.onopen = (e) => {
      // console.log('dectect isSecureLine', this.serverURL )
      if( this.serverURL.includes("wss://" )){
        this.isSecureLine = true;
      }
      this.subscribeChannelsToServer();        
      if( !this.lastState ) this.emit("statechange", "open" );
      this.lastState = true
      this.emit("open", "open" );
    };
  
    this.ws.onerror = (e) => {  
      // console.log('e.error', e )
      // console.log('e.error', e.error )
    if(this.isNode)  console.log('e.error message', e.message )
      // console.log('e.type', e.type )

    };

    this.ws.onclose = (e) => {
      console.log('e.close  code,reason', e.code , e.reason )
      this.isSecre = false;

      if( this.lastState ) this.emit("statechange","close");
      this.lastState = false
      this.emit("close",  "close" );
      if (e.code === 1009) console.log("전송 크기 초과. Over maxPayload size");
    };


  } //end connect 

  subscribeChannelsToServer(){
    if(this.channels.size > 0){
      let chList = Array.from(  this.channels )
      let chJoined = chList.join(',')
      this.sub_str_ch( chJoined)
    }
  }

  // hybrid event data listener.
  // In Node:  EventEmitter.emit( data, isBinary) , first argument is data itself.
  // In Browser:  EventTarget.distpatchEvent( event: MessageEvent),  data property of event(one argument) is data.  ( event.data)
  onWebSocketMessage(event ,isBinary ) {
    this.rxCounter++;
    this.lastTxRxTime = Date.now();
    let message ;
    let cmd = []
    if (this.isNode) {
      message = event;
      if(!isBinary){
      //In Node: It's Buffer not a String
        message = new TextDecoder().decode(message)
      }
    } else {
      message = event.data;
      isBinary = (message instanceof ArrayBuffer)

    }

// console.log('Remocon::ws:onMessage : isBinary, message' ,isBinary, message)
// console.log('type of message', typeof message )
 
    if (isBinary) {
      //type = "arraybuffer";  // client binaryType is arraybuffer.  
      this.binaryMessageParser( Buffer.from( message) ) 
    } else {
      // 일단 텍스트 프레임은 미사용.
      // this.textMessageParse(message);
    }

    // every raw message.  
    this.emit("message",  message ,isBinary );
    
  }


  binaryMessageParser( buffer ){
   let msgType = buffer[0];
   let decoded;

    console.log( "MsgType: ", MsgType[ msgType ], " LEN ", buffer.byteLength)

    if( msgType === MsgType.ENC_0808 ){
      decoded = this.sea.decrypt_remote_type_0808( buffer )
      if( decoded ){
        console.log( decoded )
        msgType = decoded[0]
        buffer = decoded 
        console.log('DECODED MsgType:', MsgType[ msgType ] )
      }else{
        console.log('DEC_FAIL', buffer.byteLength)
      }
    }
    

   switch( msgType){
     case MsgType.IAM_RES:
        let unpack = MBP.unpack( buffer, Meta.IAM_RES )
        if(unpack){
          let str = new TextDecoder().decode( unpack.$OTHERS )
          let jsonInfo = JSON.parse(str) 
          console.log('unpack iam.info', jsonInfo)
        }
      break;
     
     case MsgType.PUB_STR_CH: // simple pub , one payload, no response
      try{
        let chLen = buffer.readUint8(1)
        let chBuf = buffer.subarray(2, 2 + chLen )
        let chStr = decoder.decode(chBuf)
        let payload = buffer.subarray(2 + chLen )
        let payloadString = decoder.decode( payload )
        console.log('rcv pub_str_ch', chStr)
        console.log('rcv pub_str_ch str payload', payloadString )
        // console.log('rcv pub_str_ch payload, len', payload , payload.byteLength )
        this.emit( chStr, chStr, payloadString)
        }catch(err){
          console.log('pub_str_ch err',err)
         }
      break;
      
      case MsgType.PUB_STR_CH_RET: // High level: response, promisable, automatic unpack multiple args.
        let binObj = MBP.unpack( buffer )
        if(binObj){
          let evtName = decoder.decode( binObj.chInfo)
          let params = binObj.args 
          console.log('pub_str_ret msg ...args', ...params )
          this.emit( evtName,evtName, ...params)
        }
      break;
      
      case MsgType.RES_MID_CODE_MBP:
        let code = buffer.readUint8(1)
        let mid = buffer.readUint16BE(2)
        let meta = ( buffer.byteLength > 4  ) ?  buffer.subarray(4) : ""
        console.log(`[RES] MID: ${mid} CODE: ${code} `)
        this.testPromise( mid , code , meta)
        break;

      case MsgType.AUTH_REQ:
        let auth_hmac = this.sea.auth_hmac( buffer )
        if(auth_hmac){
          this.send( auth_hmac )
        }else{
          console.log('Invalid auth_req')
         this.close()
        }
        break;
      case MsgType.AUTH_FAIL:
          console.log('SERVER REJECT')
          // this.close()
          break;
      case MsgType.AUTH_ACK:
        if(this.sea.check_auth_ack_hmac( buffer ) ){
          // console.log(" AUTH SUCCESS:  the server is verified." )
          this.emit("authorized",  "authorized" );   
        }else{
          console.log('invalid server hmac')
          this.close()
        }
        break;
      
    

    }
  }

  // iam 은 인증에 사용하지않는다.
  iam( title ){
    console.log('iam', title)
    if(title ){
      this.send_enc_mode(  MBP.pack( 
          MB('#cmd','8', MsgType.IAM ) , 
          MB('#', title )
        ))
    }else{
      this.send_enc_mode(  MBP.pack( 
          MB('#cmd','8', MsgType.IAM )
        ))
    }
  }

  echo( args ){
    if(args ){
      console.log( 'echo args:', args )
      this.send_enc_mode(  MBP.pack( 
        MB('#type','8', MsgType.ECHO ) , 
        MB('#msg', args )
      ))
    }else{
      // null echo message use plain message ( no encryption )
      this.send( Buffer.from([ MsgType.ECHO ]))
    }
  }

  // 재정의후 사용.
  // loop( args ){
  //   if(args.length > 0 ){
  //     this.send_enc_mode(  MBP.pack( 
  //       MB('#cmd','8', MsgType.LOOP ) , 
  //       MB('#', args )
  //     ))
  //   }
  // }


  bin(...data){
    this.send( MBP.U8pack( ...data) )
  }

  // sendJSON( msg ){
  //   this.send( JSON.stringify( msg ))
  // }


  send_enc_mode( data, useEncryption = false ){

    if( this.encMode === ENC_MODE.YES || 
      this.encMode === ENC_MODE.AUTO && 
      !this.isSecureLine &&
      this.sea.isAuthorized
      )  useEncryption = true;
      

    if( useEncryption ){
      console.log('send *S*')
      let encPack = this.sea.encrypt_remote_type_0808( data ) 
      this.send( encPack )
    }else{
      console.log('send -N-')
      this.send( data )

    }

  }

  send(data) {
    
    // let len = util.getByteSize(data);
    // if (len === 0) return;

    // if (len > this.maxPayload) {
    //   console.log(" maxPayLoad over:", len);
    //   return;
    // }

    if (this.ws?.readyState === 1) {
      // console.log('send', data)
      this.txCounter++;
      this.lastTxRxTime = Date.now();
      this.ws.send(data);
    } else {
      // don't use  this.connect() , reson: too much try connect.
    }
  }


  
  setMsgPromise(mid ){
    return new Promise( (resolve, reject)=>{
      this.promiseMap.set( mid, [resolve, reject ] )
      // console.log('set promise.  mid, size', mid, this.promiseMap.size)
      setTimeout( e=>{ 
        if(this.promiseMap.has(mid )){
          reject('timeout');
          this.promiseMap.delete( mid )
          // console.log('promise timeout. mid, size:', mid, this.promiseMap.size)
        }
      }, this.promiseTimeOut);
    })
  }

  testPromise(mid , code , metaPack){
    if( this.promiseMap.has(mid)){
      console.log('res promise msg', mid)
      let [ resolve, reject ] = this.promiseMap.get( mid )
      this.promiseMap.delete( mid )
      // console.log(util.buf2hex(metaPack.buffer))
      let meta = MBP.unpack( metaPack)
      if( meta){
        if(code < 128){
          // console.log( 'unpack meta:', meta)
          resolve( meta )
        } else{
          reject( meta )
        } 

      }else{
        reject()
      }
      
    }else{
      // console.log('no promise')
    }
  }



  // publish. return promise
  publish(chInfo, ...args ){
      let mid = this.pub_str_ch_ret( chInfo, ...args)
      return  this.setMsgPromise( mid )
  }

  pub( ...args ){
      return this.pub_str_ch_ret( ...args )
  }
  // no response ,
  pub_str_ch(chInfo, oneArg ){
    if( typeof chInfo !== 'string') throw 'first parameter : channelName should be string.'
    let chEncoded = encoder.encode( chInfo)
    this.send_enc_mode(  MBP.pack( 
      MB('#cmd','8', MsgType.PUB_STR_CH) , 
      MB('#chLen','8', chEncoded.byteLength),
      MB('#chInfo', chEncoded),
      MB('#oneArgument', oneArg )
      )
      )
    }
  
  //publish only. return mid.
    pub_str_ch_ret(chInfo, ...args ){
      if( typeof chInfo !== 'string') throw 'first parameter : channelName should be string.'
      let chEncoded = encoder.encode( chInfo)
      this.send_enc_mode(  MBP.pack( 
        MB('#cmd','8',MsgType.PUB_STR_CH_RET) , 
        MB('#mid','16',++this.mid), 
        MB('#chLen','16', chEncoded.byteLength),
        MB('chInfo', chEncoded),
        MBA( ...args) ))
      // console.log('bin.pub ch:', chInfo)
      return this.mid
    }
    
 
    
  subscribe(chInfo, handler){
    let mid = this.sub( chInfo, handler)
    return  this.setMsgPromise( mid ) 
  }


  sub( ...args ){
    return this.sub_str_ch_ret( ...args )
  }
  
  sub_str_ch_ret(chInfo, handler){
    if( typeof chInfo !== 'string') throw 'first parameter : channelName should be string.'
    let chEncoded = encoder.encode( chInfo) 
    this.send_enc_mode( Buffer.concat( [
      NB('8',MsgType.SUB_STR_CH_RET),  
      NB('16', ++this.mid), 
      NB('16', chEncoded.byteLength), 
      chEncoded ]) )
    this.channels.add(chInfo)
    if(handler){  // ch#tag
      this.on( chInfo , handler)
    }
    // console.log('bin.sub ch:', chInfo)
    return this.mid 
  }
  
  sub_str_ch(chInfo, handler){
    if( typeof chInfo !== 'string') throw 'first parameter : channelName should be string.'
    let chEncoded = encoder.encode( chInfo) 
    this.send_enc_mode( Buffer.concat( [
      NB('8',MsgType.SUB_STR_CH),  
      NB('8', chEncoded.byteLength), 
      chEncoded ]) )
    this.channels.add(chInfo)
    if(handler){  // ch#tag
      this.on( chInfo , handler)
    }
    // console.log('bin.sub ch:', chInfo)
  }

  unsub_str_ch(chInfo = ""){
    console.log('unsub', chInfo)
    if( typeof chInfo !== 'string') throw 'unsub first parameter : channelName should be string.'

    this.channels.delete(chInfo)
    this.removeAllListeners( chInfo )

    let chEncoded = encoder.encode( chInfo) 
    this.send_enc_mode( Buffer.concat( [
      NB('8',MsgType.UNSUB),  
      NB('8', chEncoded.byteLength), 
      chEncoded ]) )

    // console.log('bin.sub ch:', chInfo)
  }




 
}




